@import 'components/variables';

.section-welcome {
  background-image: url(../images/bg-welcome.png);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 6rem;
  display: flex;
  align-items: center;
  border-bottom-right-radius: 40px;
  @media (min-width: 576px) {
    border-bottom-right-radius: 0;
  }
  @media (min-width: 992px) {
    padding-top: 150px;
  }
  .title {
    text-align: center;
    @media (min-width: 768px) {
      text-align: start;
    }
  }
}
.section-platos {
  background-color: #F5F8FD;
  .container {
    transform: translateY(-4rem);
    @media (min-width: 576px) {
      background: linear-gradient(to bottom, rgba(150, 221, 241, 1) 0%, rgba(255, 255, 255, 1) 8%, rgba(255, 255, 255, 1) 12%, rgba(255, 255, 255, 1) 100%);
      border-radius: 15px;
    }
    .content-platos {
      position: relative;
      padding: 4rem 1rem;
      @media (min-width: 425px) {
        padding: 4rem 3rem;
      }
      .division-vertical {
        position: absolute;
        width: 6px;
        height: calc(100% - 8rem);
        top: 4rem;
        left: 50%;
        transform: translateX(-50%);
        background-image: url(../images/punto.svg);
        background-size: contain;
        background-repeat: space;
        padding: 0;
      }
      .col-plato {
        padding: 0 2rem;
        .img-container {
          transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
          -moz-transform: rotateX(45deg);
          -o-transform: rotateX(45deg);
          position: relative;
          height: 50px;
          width: 50px;
          margin: 1.5rem auto 0;
          padding: 0;
          cursor: pointer;
          @media (min-width: 768px) {
            height: 80px;
            width: 80px;
          }
          @media (min-width: 1200px) {
            height: 90px;
            width: 90px;
          }
          .img-rhombus {
            overflow: hidden;
            height: 100%;
            width: 100%;
            display: inline-block;
            margin: 2%;
            padding: 0;
            border-radius: 10px;
            .inner-wrapper {
              position: relative;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              -ms-transform: translate(-50%, -50%);
              -webkit-transform: translate(-50%, -50%);
              -moz-transform: translate(-50%, -50%);
              -o-transform: translate(-50%, -50%);
              width: 160%;
              height: 160%;
              position: relative;
              transition: all 0.5s linear;
              img {
                transform: rotate(-45deg);
                -ms-transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
                -moz-transform: rotateX(-45deg);
                -o-transform: rotateX(-45deg);
                width: 100%;
                height: 100%;
                margin: 0;
                padding: 0;
              }
            }
          }
        }
      }
      .title {
        text-align: start;
        font-weight: 700;
        margin-bottom: 0.2rem;
      }
      p {
        margin-bottom: 0.2rem;
      }
      .price {
        font-size: 2rem;
        color: $blue;
        font-weight: 700;
      }
    }
    #page_navigation {
      text-align: center;
      .previous_link,
      .next_link {
        font-size: 1.5rem;
        color: $text-gray;
        margin: 0 1rem;
        &:hover {
          color: $blue;
        }
      }
      .page_link {
        color: $text-gray;
        margin: 0 0.5rem;
        text-decoration: none;
        font-size: 1.5rem;
        font-weight: 700;
        &.active_page {
          color: $blue;
        }
      }
    }
  }
}